#x-swiper {
	.swiper-slide {
		height: 200px;
		text-align: center;
		color: #fff;
		font-size: 40px;
		line-height: 200px;
	}
	.grey {
		background: grey;
	}
	.blue {
		background: #FF8604;
	}
	.red {
		background: pink;
	}
	.swiper-pagination-bullet {
		text-align: center;
		line-height: 20px;
		width: 20px;
		height: 20px;
		color: #fff;
	}
}